@import url('https://fonts.googleapis.com/css2?family=Jacquard+12&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&family=Jacquard+12&display=swap');

body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

h1,
h2 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  max-width: 100%;
  box-sizing: border-box;
}

.hide-on-small-screen {
  display: none;
}

@media (min-width: 800px) {
  .hide-on-small-screen {
    display: block;
  }
}

.width-on-small-screen {
  width: 90%;
  font-size: .7em;
}

@media (min-width: 800px) {
  .width-on-small-screen {
    width: 50%;
    font-size: 1.5em;
  }
}

.reduce-padding-on-small-screen {
  padding: 50px 0px 0px 0px;
  text-align: center;
  justify-content: center;
}

@media (min-width: 800px) {
  .reduce-padding-on-small-screen {
    padding: 100px;
    justify-content: space-between;
    text-align: left;
  }
}

.container {
  background-color: rgb(255, 255, 255);
  width: 100%;
}

.toolbar {
  position: absolute;
  width: 100%;
  display: block;
}

.toolbar-content {
  width: 100%;
  height: 50px;
  color: rgb(255, 255, 255);
  background: #333333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-item {
  padding: 4em;
}

.right-item {
  display: flex;
  padding: 1em;
}

.right-item p {
  padding: .4em;
}

.clic {
  cursor: pointer;
}

.line {
  position: relative;
  height: 5px;
  background: linear-gradient(to right,#1a0b52 1%, #0d10b8 50%, #1a0b52 100%);
  overflow: hidden;
}

.line::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 50%;
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 1%,
    rgba(79, 120, 197, 0.8) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  animation: shine 3s infinite;
}

@keyframes shine {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}

.top {
  background: linear-gradient(45deg, #0d0d3a 10%, #000000 100%);
}

.min-text-size {
  min-height: 200px;
  max-height: 250px;
  
}

@media (min-width: 800px) {
  .min-text-size {
    min-height: 700px;
    max-height: 800px;
  }
}

.space {
  width: 100%;
  height: 50px;
}

.content {
  display: flex;
  align-items: center;
  font-family: Arial;
  font-size: 1.5em;
  color: rgba(255, 255, 255, 0.8);
}

.wave {
  width: 100%;
  height: 300px;
  /* z-index: -1; */
}

.wave-color {
  background: linear-gradient(30deg, rgb(18, 18, 75) 10%, rgb(3, 3, 24) 100%);
}

.wave-color2 {
  position: relative;
  background-color: white;
}

.middle {
  position: relative;
  background-color: #ffffff;
}

.content-middle {
  display: flex;
  align-items: center;
  font-family: Arial;
  font-size: 1.5em;
}

.button {
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  text-align: center;
}

.button-green {
  background-color: green;
}

.button-green:hover {
  background-color: darkgreen;
}

.button-blue {
  background-color: blue;
}

.button-blue:hover {
  background-color: darkblue;
}

.botton {
  background: #000000;
  width: 100%;
  overflow: hidden;
}

.content-botton {
  display: flex;
  padding: 1em;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  box-sizing: border-box; 
}

.face {
  width: 10em;
  height: 10em;
  max-width: calc(100% - 2em);
}

.contact-logo {
  height: 100px;
  width: 100px;
}

@media (max-width: 600px) {
  .contact-logo {
    height: 80px;
    width: 80px;
  }
}

@media (max-width: 400px) {
  .contact-logo {
    height: 60px;
    width: 60px;
  }
}

.scroll-container {
  width: 100%;
  overflow-x: auto; 
  white-space: nowrap;
}

.scroll-content {
  display: inline-block;
  transition: transform 0.3s ease;
}

.scroll-content img {
  width: auto; 
  height: auto; 
  margin-right: 20px; 
}
